<template>
  <div class="entMain">
    <div class="content">
      <el-row :gutter="20">
        <el-col :span="12" :offset="5">
          <div class="content-left">
            <div class="title">
              <span>
                <b>面试邀请</b>
                ({{sentJob.length}})
              </span>
            </div>
            <div v-if="sentJob.length==0">
              <p>暂无邀请</p>
            </div>

            <el-table v-else :data="tempList" stripe style="width: 100%" size="small">
              <el-table-column prop="sendTime" label="日期" sortable width="120"></el-table-column>
              <el-table-column prop="jobName" label="职位" width="120"></el-table-column>
              <el-table-column prop="entName" label="公司"></el-table-column>
              <el-table-column prop="status" label="状态"></el-table-column>
              <el-table-column label="操作" width="100">
                <template slot-scope="scope">
                  <el-button
                    @click="funClose(scope.row)"
                    type="text"
                    size="small"
                    style="color:#f56c6c;"
                  >拒绝邀请</el-button>
                  <router-link
                    :to="{name: 'singlejob', query:{jobid: scope.row.jobid,entid:scope.row.entid,resumeid:scope.row.resumeid}}"
                    target="_blank"
                  >
                    <el-button type="text" size="small" @click="funShow(scope.row)">查看职位</el-button>
                  </router-link>

                  <el-button
                    @click="funPass(scope.row)"
                    type="text"
                    size="small"
                    style="color:#67c23a"
                  >接受邀请</el-button>
                </template>
              </el-table-column>
            </el-table>

            <el-pagination
              layout="prev, pager, next"
              :total="sentJob.length"
              style="text-align:center;"
              class="page"
              @size-change="funSizeChange"
              @current-change="funCurrentChange"
              :current-page="currentPage"
              :page-size="pageSize"
            ></el-pagination>
          </div>
        </el-col>
      </el-row>
    </div>

    <!-- <hr> -->
    <!-- <hr> -->
  </div>
</template>

<script>
import Cookies from "vue-cookies";
export default {
  name: "entMain",
  data() {
    return {
      sentJob: [],
      entJobInfo: [],
      currentPage: 1,
      pageSize: 10,
      tempList: []
    };
  },
  created() {
    this.axios
      .post("getSentJob", {
        psnid: Cookies.get("psnid"),
        entid: ""
      })
      .then(data => {
        if (data.data.status == "ok") {
          let arr = JSON.parse(data.data.msg);
          for (var i in arr) {
            if (arr[i].fields.status == "面试邀请") {
              this.sentJob.push(arr[i].fields);
            }
          }

          this.tempList = this.sentJob.slice(0, 10);
        }
      });
  },
  methods: {
    funClose(item) {
      this.sentJob.splice(this.sentJob.indexOf(item), 1);
      this.axios
        .post("closeSent", {
          msg: {
            jobid: item.jobid,
            resumeid: item.resumeid,
            entid: item.entid,
            psnid: item.psnid
          }
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.$message({
              type: "success",
              message: data.data.msg
            });
            this.tempList = this.sentJob.slice(0, 10);
          }
        });
    },
    funPass(item) {
      this.sentJob.splice(this.sentJob.indexOf(item), 1);
      this.axios
        .post("passSent", {
          msg: {
            jobid: item.jobid,
            resumeid: item.resumeid,
            entid: item.entid,
            psnid: item.psnid
          }
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.$message({
              type: "success",
              message: data.data.msg
            });
            this.tempList = this.sentJob.slice(0, 10);
          }
        });
    },
    funDelJob(id, index) {
      this.entJobInfo.splice(index, 1);
      this.axios
        .post("delEntJobInfo", {
          msg: id
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.$message({
              type: "success",
              message: data.data.msg
            });
          }
        });
    },

    funSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.funCurrentChange(this.currentPage);
    },
    funCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.currentChangePage(this.sentJob, currentPage);
    },
    //分页方法
    currentChangePage(list, currentPage) {
      let from = (currentPage - 1) * this.pageSize;
      let to = currentPage * this.pageSize;
      this.tempList = [];
      for (; from < to; from++) {
        if (list[from]) {
          this.tempList.push(list[from]);
        }
      }
    }
  },
  computed: {}
};
</script>
<style scoped>
.content {
  padding: 60px;
  color: #303133;
}
.content-left {
  padding-bottom: 20px;
}
.content-left .title {
  font-size: 16px;
  color: #606266;
}
.content-left .el-tag {
  font-size: 12px;
  color: #606266;
}

.el-tag + .el-tag {
  margin: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
.content-left p {
  font-size: 16px;
  color: #606266;
}
.content-right .box-card {
  background: #f8f8f9;
}
article {
  padding: 10px;
}
.comment {
  padding: 60px;
  /* background: #67c23a; */
  height: 36px;
  color: #303133;
  font-size: 18px;
  line-height: 36px;
}

.job-list {
  font-size: 14px;
  color: #606266;
}
.title {
  border-bottom: 1px solid #ddd;
  padding-bottom: 20px;
}
</style>
